.app {
  margin: 0 auto;
  max-width: 800px;

  border-radius: 10px;

  overflow: hidden;
}

.main {
  height: 100%;
  min-height: 450px;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.info {
  padding: 10px;
  min-width: 340px;

  background-color: #94bbe9;
  background-image: linear-gradient(
    90deg,
    #eeaeca 0%,
    #ceb3d5 20%,
    #94bbe9 100%
  );

  align-self: stretch;
}

.start {
  margin: 10px 5px;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: #584158;
}
